.datasetbox{
    height: 5em; 
    width: 5em;
    border-bottom: 4px solid #CDCDCD;
    margin: 0px 10px 0px 0px;
    opacity: 0.5;

}
.datasetbox:hover{
    /* border-bottom: 4px #0062FF solid; */
    opacity: 1;
}

.datasetbox.active{
    border-bottom: 4px #0062FF solid;
    opacity: 1;
}

.mynotif{
    border-left: 4px  #054ADA solid;
}

.datasettitles{
    margin: 0px 0px 5px 0px;
    font-weight: bold;
    font-size: 0.72em;
}

.datasetfullbox:hover{
    /* background-color: blue; */
}

.dsselected {
    border-bottom: 4px #0062FF solid;
}

.mainsimilarityimage{
    height: 13em;
    border-bottom: 4px #0062FF solid;
}
.mainsimilaritydesc{
    font-size: 1em;
    line-height: 1.28em; 
    
}

.simiimage{
    height: 5em; 
    margin: 0px 0px 0px 0px; 
    /* border-bottom: 4px solid #CDCDCD; */
}

.simiimage:hover{
    opacity: 0.3;
    /* border-bottom: 4px #0062FF solid; */
    /* transition: opacity 0.3s;
    -webkit-transition: opacity 0.3s; */
}
.simiimage.active{
    border-bottom: 4px #0062FF solid;
    opacity: 1;
}
.dispblock{
    display: block;
}

.outersimbar{
    background: #FFD78F;
    height: 4px;
}

.innersimbar{
    background: #FFA500;
    height: 4px;
}

.similarityscorebox{
    position: absolute;
    top:2em;
    left: 0em;
    font-weight: bold;
    pointer-events: none;
    width: 100%;
    text-align: center;
    opacity: 0;
}

.similarityfullbox:hover > .similarityscorebox{
    opacity: 1;

}

.smalldesc{
    font-size: 0.72em;

}